
'use client';
import { useState, useEffect } from 'react';
import { Button } from '@/components/ui/button';
import { Plus, Upload, Users, ArrowRight, BookCopy, Settings, Calendar, Share2 } from 'lucide-react';
import { Header } from '@/components/layout/Header';
import { useAuth } from '@/hooks/use-auth';
import { useRouter } from 'next/navigation';
import Link from 'next/link';
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from '@/components/ui/card';
import AppShell from '@/components/layout/AppShell';
import type { Event, ServiceTemplate, Volunteer } from '@/types';
import { Loader2 } from 'lucide-react';

export default function AdminPage() {
  const { dataService, churchId, isAdmin, isLoggedIn } = useAuth();
  const router = useRouter();

  const [events, setEvents] = useState<Event[]>([]);
  const [volunteers, setVolunteers] = useState<Volunteer[]>([]);
  const [templates, setTemplates] = useState<ServiceTemplate[]>([]);
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    if (!isLoggedIn) {
      router.push('/');
    } else if (!isAdmin) {
       router.push('/my-schedule');
    }
  }, [isLoggedIn, isAdmin, router]);

  useEffect(() => {
    if (!dataService || !churchId) return;

    setIsLoading(true);
    const unsubs: (() => void)[] = [];

    unsubs.push(dataService.getEvents(churchId, setEvents));
    unsubs.push(dataService.getVolunteers(churchId, setVolunteers));
    unsubs.push(dataService.getServiceTemplates(churchId, setTemplates));

    // A simple way to check if initial loading is done.
    const loadingTimeout = setTimeout(() => setIsLoading(false), 1500);
    unsubs.push(() => clearTimeout(loadingTimeout));

    return () => unsubs.forEach(unsub => unsub());

  }, [dataService, churchId]);

  
  if (!isLoggedIn || !isAdmin || !dataService) {
    return null; // or a loading spinner while redirecting
  }

  return (
    <AppShell>
      <div className="flex min-h-screen w-full flex-col bg-muted/40">
        <Header />
        <main className="flex flex-1 flex-col gap-4 p-4 md:gap-8 md:p-8">
          <div className="flex items-center justify-between">
            <h1 className="text-3xl font-bold font-headline tracking-tight">
              Admin Console
            </h1>
          </div>
          {isLoading ? (
             <div className="flex justify-center items-center h-64">
                <Loader2 className="h-8 w-8 animate-spin text-muted-foreground" />
             </div>
          ) : (
          <div className="grid gap-6 md:gap-8 lg:grid-cols-2 xl:grid-cols-3">
              <Card className="lg:col-span-1 xl:col-span-1">
                  <CardHeader>
                      <CardTitle>Service Management</CardTitle>
                      <CardDescription>
                          Manage, create, and import church services and events.
                      </CardDescription>
                  </CardHeader>
                  <CardContent>
                      <div className="flex items-center gap-2 text-sm text-muted-foreground">
                          <Calendar className="h-5 w-5" />
                          <span>{events.length} Upcoming Services</span>
                      </div>
                  </CardContent>
                  <CardFooter>
                      <Button asChild>
                          <Link href="/admin/services">
                              Manage Services <ArrowRight className="ml-2 h-4 w-4" />
                          </Link>
                      </Button>
                  </CardFooter>
              </Card>

              <Card className="lg:col-span-1 xl:col-span-1">
                  <CardHeader>
                      <CardTitle>Volunteer Management</CardTitle>
                      <CardDescription>
                          View volunteer profiles, manage assignments, and invite new members.
                      </CardDescription>
                  </CardHeader>
                  <CardContent>
                      <div className="flex items-center gap-2 text-sm text-muted-foreground">
                          <Users className="h-5 w-5" />
                          <span>{volunteers.length} Volunteers</span>
                      </div>
                  </CardContent>
                  <CardFooter>
                      <Button asChild>
                          <Link href="/admin/volunteers">
                              Manage Volunteers <ArrowRight className="ml-2 h-4 w-4" />
                          </Link>
                      </Button>
                  </CardFooter>
              </Card>

              <Card className="lg:col-span-1 xl:col-span-1">
                  <CardHeader>
                      <CardTitle>Role &amp; Template Management</CardTitle>
                      <CardDescription>
                        Define universal roles and create service templates for quick setup.
                      </CardDescription>
                  </CardHeader>
                  <CardContent>
                      <div className="flex items-center gap-2 text-sm text-muted-foreground">
                          <BookCopy className="h-5 w-5" />
                          <span>{templates.length} Templates</span>
                      </div>
                  </CardContent>
                  <CardFooter>
                      <Button asChild>
                          <Link href="/admin/templates">
                              Manage Templates <ArrowRight className="ml-2 h-4 w-4" />
                          </Link>
                      </Button>
                  </CardFooter>
              </Card>

               <Card className="lg:col-span-1 xl:col-span-2">
                  <CardHeader>
                      <CardTitle>Share &amp; Export</CardTitle>
                      <CardDescription>
                        Generate bulletin content and get your public website widget.
                      </CardDescription>
                  </CardHeader>
                   <CardContent>
                      <div className="flex items-center gap-2 text-sm text-muted-foreground">
                          <Share2 className="h-5 w-5" />
                          <span>Formatted exports for newsletters and websites.</span>
                      </div>
                  </CardContent>
                  <CardFooter>
                      <Button asChild>
                          <Link href="/admin/share">
                              Share Schedule <ArrowRight className="ml-2 h-4 w-4" />
                          </Link>
                      </Button>
                  </CardFooter>
              </Card>
              
              <Card className="lg:col-span-1 xl:col-span-1">
                  <CardHeader>
                      <CardTitle>Church Settings</CardTitle>
                      <CardDescription>
                        Update your church's information and primary contact details.
                      </CardDescription>
                  </CardHeader>
                  <CardFooter>
                      <Button asChild>
                          <Link href="/admin/settings">
                              Manage Settings <ArrowRight className="ml-2 h-4 w-4" />
                          </Link>
                      </Button>
                  </CardFooter>
              </Card>
          </div>
          )}
        </main>
      </div>
    </AppShell>
  );
}
